
import { mapGetters, mapMutations } from "vuex";
import headTop from "@/components/header";
import footerNav from "@/components/footer";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import Api from "@/js/common";
import noData from "@/components/noData";

export default {
data() {
return {
isNoData: false,
page: 1,
pageSize: 10,
list: [],
data: [],
getBarWidth: function(index) {
return 1 * 10 + "rem";
},
position: 0
};
},
methods: {
...mapMutations("submit", ["newAnswer"]),
goto(id, quesWj) {
this.newAnswer("del");
this.$router.push({
path: `/toDo/${id}/${quesWj}`
});
},
getQustionList() {
let vmSelf = this;
var submitObj = {
value: 1,
page: vmSelf.page
};
vmSelf.$vux.loading.show({
text: "Loading"
});
Api.qustionList(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide();
vmSelf.list = response;
if (vmSelf.page === 1) {
vmSelf.$refs.myscroller.finishPullToRefresh();
vmSelf.data = [];
}
let start = vmSelf.list.length;
for (let i = 0; i < start; i++) {
vmSelf.data.push(vmSelf.list[i]);
}
if (vmSelf.data.length === 0) {
vmSelf.isNoData = true;
} else {
vmSelf.isNoData = false;
}
if (start) {
vmSelf.page++;
vmSelf.$refs.myscroller.finishInfinite(0);
} else {
return vmSelf.$refs.myscroller.finishInfinite(2);
}
})
.catch(function(err) {
console.log("=====", err);
});
},
loadMore() {
if (this.page === 1) {
return this.$refs.myscroller.finishInfinite(2);
}
this.getQustionList();

},
refresh() {
this.data.length = 0;
this.page = 1;
this.getQustionList();
this.$refs.myscroller.finishPullToRefresh();
}
},
computed: {
...mapGetters(["isLogin"])
},
mounted() {
if (!this.isLogin) {
this.isNoData = true;
} else {
this.isNoData = false;
this.getQustionList();
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name === "creatQuestion") {
vm.data = [];
vm.page = 1;

} else {
next();
}
});
},
beforeRouteLeave(to, from, next) {
this.position = this.$refs.myscroller.getPosition();
next();
},
activated() {
if (this.position.top) {
var self = this;
self.$refs.myscroller.scrollBy(
self.position.left,
self.position.top,
false
);
self.position = {};
}
},
components: {
headTop,
Tab,
TabItem,
Badge,
footerNav,
ViewBox,
noData
}
};



